import React, {useRef, useState} from "react";
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';

import _ from 'lodash'
import Tooltip from 'react-bootstrap/Tooltip';
import Overlay from 'react-bootstrap/Overlay';
import {lang} from "../language";
import {table} from "../const"

export const ShemaJoinTable = (TabItems) => {
    const text = table[lang]

    //console.log(TabItems);
    //console.log(TabItems.TabItems);
    const [tooltip, setTooltip] = useState(false);
    const localTitleView = localStorage.getItem('titleView') ? localStorage.getItem('titleView') : 'tree'

    const [titleView, setTitleView] = useState(localTitleView)
    const target = useRef(null);

    const localIndents = localStorage.getItem('indents') ? localStorage.getItem('indents') : '- '

    const [indents, setIndents] = useState(localIndents)

    // if (!indents) {
    //     setIndents('   ')
    // }
    // else {
    //     setIndents('- ')
    // }
    const copyTableNew = () => {
        const elTable = document.querySelector('#table-to-copied');
        let range, sel;
        // Ensure that range and selection are supported by the browsers
        if (document.createRange && window.getSelection) {

            range = document.createRange();
            sel = window.getSelection();
            // unselect any element in the page
            sel.removeAllRanges();

            try {
                range.selectNodeContents(elTable);
                sel.addRange(range);
            } catch (e) {
                range.selectNode(elTable);
                sel.addRange(range);
            }
            setTooltip(true)
            setTimeout(() => {
                setTooltip(false)
            }, 1000)
            document.execCommand('Copy');
        }

        sel.removeAllRanges();

        console.log('Element Copied! Paste it in a file')
    }

    const maxStep = _.maxBy(TabItems.TabItems, function (o) {
        return o.step;
    }).step;

    let td = (tStep) => {
        let content = [];
        for (let i = 0; i < tStep; i++) {
            content.push(<td key={i.toString()}></td>);
        }
        return content;
    };
    let toStep = (tStep) => {
        let content = [];
        for (let i = 0; i < tStep; i++) {
            content.push(indents);
        }
        return content;
    };


    return (
        <>
            {/*<pre>{JSON.stringify(TabItems, null, 4)}</pre>*/}
            <div className='d-flex justify-content-end'>
                <div style={{maxWidth: '500px'}}>
                    <InputGroup>
                        <InputGroup.Text id="inputGroup">Отображение атрибута</InputGroup.Text>
                        <Form.Select xs="1" aria-label="Отображение названия" value={titleView}
                                     onChange={e => {
                                         //console.log("e.target.value", e.target.value);
                                         setTitleView(e.target.value);
                                         localStorage.setItem('titleView',e.target.value);
                                     }}>
                            <option value="tree">Древовидная структура</option>
                            <option value="path">Полный путь</option>
                            <option value="indents">Отступы</option>
                        </Form.Select>
                    </InputGroup>
                </div>
                {titleView==='indents' && <Form.Control className="ms-3" style={{maxWidth: '60px'}}
                    type="text"
                    maxLength={3}
                    onChange={e => {
                        let ind = e.target.value.replace(' ',' ');
                        setIndents(ind);
                        localStorage.setItem('indents',ind);
                    }}
                    value={indents}
                />}

                <div className="ms-auto">Second item</div>
                <Button variant="outline-primary" lassName="rounded-5" ref={target} id="btn-copy-table"
                        onClick={copyTableNew}>{text.copyTable}</Button>
                <Overlay target={target.current} show={tooltip} placement="top">
                    {(props) => (
                        <Tooltip id="overlay-example" {...props}>
                            {text.copied}
                        </Tooltip>
                    )}
                </Overlay>

            </div>
            <br/>
            <Table id="table-to-copied" bordered hover>
                <thead>
                <tr>
                    {/*{th()}*/}
                    {titleView === 'tree' && <th colSpan={maxStep + 1}>{text.title}</th>}
                    {titleView === 'path' && <th>{text.title}</th>}
                    {titleView === 'indents' && <th>{text.title}</th>}
                    <th>{text.type}</th>
                    <th>{text.description}</th>
                    <th>{text.required}</th>
                </tr>
                </thead>
                <tbody>
                {TabItems.TabItems.map((TabItem, index) =>
                    <tr key={index}>
                        {titleView === 'tree' && td(TabItem.step)}
                        {titleView === 'tree' &&
                            <td colSpan={maxStep + 1 - TabItem.step}>{TabItem.title && TabItem.title}</td>}
                        {titleView === 'path' && <td>{TabItem.path}</td>}
                        {titleView === 'indents' && <td>{toStep((TabItem.step))}{TabItem.title}</td>}
                        <td>{TabItem.type}</td>
                        <td>{TabItem.description && <>{TabItem.description}</>}
                        </td>
                        <td>
                            {TabItem.required && <>required<br/></>}
                            {TabItem.pattern !== undefined && <>pattern: {TabItem.pattern}<br/></>}
                            {TabItem.multipleOf !== undefined && <>multipleOf: {TabItem.multipleOf}<br/></>}
                            {TabItem.const !== undefined && <>const: {TabItem.const}<br/></>}
                            {TabItem.uniqueItems !== undefined && <>uniqueItems: {TabItem.uniqueItems}<br/></>}
                            {TabItem.default !== undefined && <>default: {TabItem.default}<br/></>}
                            {TabItem.exclusiveMinimum !== undefined && <>exclusiveMinimum: {TabItem.exclusiveMinimum} <br/></>}
                            {TabItem.exclusiveMaximum !== undefined && <>exclusiveMaximum: {TabItem.exclusiveMaximum} </>}
                            {TabItem.minItems !== undefined && <>minItems: {TabItem.minItems} <br/></>}
                            {TabItem.maxItems !== undefined && <>maxItems: {TabItem.maxItems}<br/> </>}
                            {TabItem.minimum !== undefined && <>minimum: {TabItem.minimum} <br/></>}
                            {TabItem.maximum !== undefined && <>maximum: {TabItem.maximum} <br/></>}
                            {TabItem.minLength !== undefined && <>minLength: {TabItem.minLength} <br/></>}
                            {TabItem.maxLength !== undefined && <>maxLength: {TabItem.maxLength} <br/></>}
                            {TabItem.format !== undefined && <>format: {TabItem.format} <br/></>}
                            {TabItem.example && <>example: {TabItem.example} <br/></>}
                            {TabItem.enum !== undefined && <>enum: {TabItem.enum.map(txt => <>{txt}; </>)} <br/></>}
                        </td>
                    </tr>,
                )}
                </tbody>
            </Table>
        </>
    );
};
