import React from "react";

export const RecRenderArray = (schema) => {
    const Items = []
    //const escAttribs = ["$ref","type",]
    RecSchemaTraversal(Items, schema, schema, 0)
    return Items
    // return(
    //   <>
    //     <pre>{JSON.stringify(schema, null, 4)}</pre>
    //   </>
    // );
};
//Main driver of the recursive schema traversal.
const RecSchemaTraversal = (Items, base, schema, step, title, required, path, isRef) => {
    //if(!schema || typeof schema!='object') return;
    // if (title == 'isLcRequired') {
    //   debugger
    // }
    const ref = schema.$ref ? schema.$ref.split("/").pop() : undefined;
    let type = ref ? ref : schema.type
    let exclusiveMaximum = (typeof schema.exclusiveMaximum === 'boolean') ? schema.exclusiveMaximum.toString() : schema.exclusiveMaximum
    let exclusiveMinimum = (typeof schema.exclusiveMinimum === 'boolean') ? schema.exclusiveMinimum.toString() : schema.exclusiveMinimum
    let uniqueItems = (typeof schema.uniqueItems === 'boolean') ? schema.uniqueItems.toString() : schema.uniqueItems
    //console.log(schema.pattern)
    let attrib = []

    let canPush = true
    if (isRef && (schema.type == 'object' || schema.type == 'array')) {
        canPush = false
    }

    if (isRef && canPush) {
        step++
    }
    //isRef && schema.type != 'object' ? false : true
    canPush && (title != 'oneOf') && (path = path ? path + "." + title : title)
    // console.log(title)
    // for (let key in schema){
    //     console.log("   ",key, schema[key],schema[key].toString())
    // }

    canPush && Items.push(
        {
            title,
            //path+"."title,
            step,
            path: title != 'oneOf' ? path : path + ' oneOf 🔀',
            description: schema.description,
            type: type,
            required: required ? true : false,
            pattern: schema.pattern,
            minimum: schema.minimum,
            maximum: schema.maximum,
            default: schema.default,
            const: schema.const,
            exclusiveMaximum: exclusiveMaximum,
            exclusiveMinimum: exclusiveMinimum,
            multipleOf: schema.multipleOf,
            maxLength: schema.maxLength,
            minLength: schema.minLength,
            enum: schema.enum,
            format: schema.format,
            minItems: schema.minItems,
            maxItems: schema.maxItems,
            uniqueItems: uniqueItems,
            example: schema.example,

        },
    );
    if (schema.items instanceof Array) {
        schema.items.map((s, i) => {
            RecSchemaTraversal(Items, base, s, step + 1)
        })
    }
    if (schema.oneOf) {
        schema.oneOf.map((s, i) => {
            RecSchemaTraversal(Items, base, s, step + 1, 'oneOf', required, path)
        })
    }
    if (schema.allOf) {
        schema.allOf.map((s, i) => {
            RecSchemaTraversal(Items, base, s, step, 'allOf', required, path, true)
        })
    }
    if (schema.items instanceof Object) {
        RecSchemaTraversal(Items, base, schema.items, step + 1, title + "[n]", false, path)
    }
    if (schema.properties) {
        Object.entries(schema.properties).map(([titleItem, s], i) => {
            const schem = s;
            RecSchemaTraversal(Items, base, schem, step + 1, titleItem, schema.required && schema.required.includes(titleItem), path)
        })
    }
    //ref пока смотрим только в рамках одного файла в блоке definitions
    //надо выше поднять и если тип примитивный, свойства сразу выводить
    if (ref) {
        if(base.definitions[ref].type == 'string'){
            console.log(ref,base.definitions[ref])
        }
        RecSchemaTraversal(Items, base, base.definitions[ref], step, ref, required, path, true) //required && schema.required.includes(ref)
    }
}



