import 'bootstrap/dist/css/bootstrap.min.css';
import {RecRenderArray} from "./containers/RecRenderArray";
import {ShemaTable} from "./containers/ShemaTable";
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import React, {useState} from "react";
import {ShemaJoinTable} from "./containers/ShemaJoinTable";
import {SchemaExemple} from "./SchemaExempe";

import AceEditor from "react-ace";
import "ace-builds/src-noconflict/mode-json";
import "ace-builds/src-noconflict/theme-xcode";
import "ace-builds/src-noconflict/ext-language_tools";
import Toast from 'react-bootstrap/Toast';
import Alert from 'react-bootstrap/Alert';
import {general} from "./const";
import {lang} from "./language";
import telegranIcon from "./img/icons8-telegram-24.svg"

function App() {
    const text = general[lang]
    const [code, setCode] = useState(
        SchemaExemple
    );
    const [view, setView] = useState(
        JSON.parse(SchemaExemple)
    );
    const [showAlert, setShowAlert] = useState(false);
    const [errorText, setErrorText] = useState('');

    const Generating = (code) => {
       // let parse = JSON.parse(code)
        try {
            let parse = JSON.parse(code)
            //console.log(parse)
            setShowAlert(false)
            setView(parse)
        }
        catch (e){
            console.log('JSON is not valid')
            //console.log(e.toString())
            setErrorText(e.toString())
            setShowAlert(true)
        }
    }
  const schema = require("./schema.json");
  const x = RecRenderArray(view)//view);
  //console.log(schema.type);
  return (
      <Container className="p-3">
          <Container className="p-3 mb-4 bg-light rounded-3">

              <div className='d-flex justify-content-end'><telegram/>
                  <Button href="https://t.me/farakshin" target="_blank" variant="outline-primary"  >Обратная связь <img src={telegranIcon}/>
                  </Button>
                  {/*<Button variant="outline-primary" className="rounded-5 m-1">EN</Button>*/}
              </div>
              <h1 className="header">{text.header}</h1>
              {/*<FormTextarea value={schema}/>*/}

              {/*<div className='mb-3 d-flex justify-content-end'>*/}
              {/*    <Button variant="primary" disabled = {code ? false : true}  onClick={Generating}>   Run   </Button>*/}
              {/*</div>*/}
              <div className=" mb-4">
              <AceEditor
                  style={{ maxHeight: '400px', width: '100%',borderRadius: "8px"}}
                  placeholder={text.placeholder}
                  mode="json"
                  theme="xcode"
                  name="blah2"
                  //onLoad={this.onLoad}
                  onChange={code => {setCode(code); Generating(code)}}
                  fontSize={14}
                  showPrintMargin={false}
                  showGutter={true}
                  highlightActiveLine={true}
                  value={code}
                  setOptions={{
                      enableBasicAutocompletion: true,
                      enableLiveAutocompletion: true,
                      enableSnippets: true,
                      showLineNumbers: true,
                      tabSize: 2,
                  }}/>
              </div>

              {/*<div style={{ maxHeight: '400px', overflowY: "scroll", background: "#282c34",borderRadius: "8px",color: "#fff", }}*/}
              {/*    className=" mb-4 ">*/}
              {/*    <SimpleEditor />*/}
              {/*</div>*/}
              {showAlert==false ? <div className="p-2 pt-4"  style={{  overflowY: "scroll", background: "#fff",borderRadius: "8px",color: "#fff", }}>
                  <ShemaJoinTable TabItems={x}/>
                  {/*<h1>test</h1>*/}
                  {/*<ShemaTable TabItems={x}/>*/}
              </div> :
                  <Alert  variant={"danger"} >
                      JSON is not valid <br/>
                      {errorText}
                  </Alert>
              }
          </Container>
      </Container>
  );
}

export default App;
